<template>
  <div class="center">
    <div class="d-flex justify-content-center align-items-center flex-column"
      :class="{ shakeWrong : wrong, shakeRight : right }">
      <img
        :src="images[count]"
        @click="turnOn"
        alt="red_button"
        @keydown="turnOn"
        style="width: 400px"
      />
      <div class="text-center text-white mt-5 w-75 h2" :show="wrong">
        <div class="error">
        {{ error }}
        </div>
      </div>
    </div>
    <!-- <button @click="turnOn" type="button"
      class="btn btn-primary btn-lg">{{ count }}</button> -->
  </div>
</template>

<script>
import defaultButton from '../assets/buttons/default_button.svg';
import errorButton from '../assets/buttons/red_button.svg';
import successButton from '../assets/buttons/green_button.svg';

export default {
  name: 'SendRequest',
  data() {
    return {
      images: [defaultButton, successButton, errorButton],
      count: 0,
      right: false,
      wrong: false,
      error: '',
    };
  },
  methods: {
    test() {
      this.count += 1;
      console.log('hihi');
    },
    turnOn() {
      this.axios
        .post(`${window.location.protocol}//${window.location.hostname}:3000/turnOn`)
        .then((response) => {
          if (response.data === 'OK\n') {
            console.log(response.data);
            this.count = 1;
            this.right = true;
            setTimeout(() => {
              this.right = false;
              this.count = 0;
            }, 1500);
            this.right = true;
          } else {
            this.count = 2;
            this.wrong = true;
            this.error = response.data.toLowerCase();
            setTimeout(() => {
              this.wrong = false;
              this.count = 0;
              this.error = '';
            }, 2500);
            console.log(response.data);
            console.log(response);
          }
        });
    },
  },
};
</script>

